/* Cosmic background layers */
body::before {
	content: '';
	position: fixed;
	inset: 0;
	background: radial-gradient(circle at 20% 30%, rgba(124, 58, 237, 0.16), transparent 45%),
		radial-gradient(circle at 80% 70%, rgba(236, 72, 153, 0.12), transparent 50%),
		radial-gradient(circle at 50% 50%, rgba(59, 130, 246, 0.1), transparent 55%);
	animation: float 18s ease-in-out infinite;
	z-index: -2;
	pointer-events: none;
}

body::after {
	content: '';
	position: fixed;
	inset: 0;
	background: linear-gradient(120deg, rgba(255, 255, 255, 0.02), rgba(255, 255, 255, 0));
	opacity: 0.6;
	mix-blend-mode: screen;
	z-index: -3;
	pointer-events: none;
}

@keyframes float {
	0%,
	100% {
		transform: translate(0, 0) scale(1);
	}
	33% {
		transform: translate(30px, -20px) scale(1.05);
	}
	66% {
		transform: translate(-20px, 15px) scale(0.98);
	}
}

/* Custom scrollbar */
.custom-scroll {
	scrollbar-width: thin;
	scrollbar-color: rgba(139, 92, 246, 0.6) transparent;
}

.custom-scroll::-webkit-scrollbar {
	width: 8px;
}

.custom-scroll::-webkit-scrollbar-track {
	background: transparent;
}

.custom-scroll::-webkit-scrollbar-thumb {
	background: linear-gradient(180deg, rgba(139, 92, 246, 0.8), rgba(236, 72, 153, 0.7));
	border-radius: 9999px;
}

/* Cosmic card hover effects */
.cosmic-card {
	transition: transform 200ms ease, box-shadow 300ms ease, border-color 200ms ease;
}

.cosmic-card:hover {
	transform: translateY(-2px);
	box-shadow: 0 8px 24px rgba(124, 58, 237, 0.2), 0 0 16px rgba(236, 72, 153, 0.15);
	border-color: rgba(139, 92, 246, 0.3);
}

